<template>
    <div class="predict">
        <div>ai</div>
        <div style="margin-bottom: 20px;">
            :
            <a-input-number :min="1" :max="1000" v-model="count"/>
            <a-button @click="getWinner" :loading="!!total"></a-button>
            {{total ? ' ' + total : ''}} 1{{winner0}}({{Math.floor(winner0/count*100)}}%)
            2{{winner1}}({{Math.floor(winner1/count*100)}}%)
        </div>
        <a-table
                :columns="columns"
                :rowKey="r => r.teamId + '_' + r.index + '_' +r.no"
                :dataSource="data"
                size="small"
                :pagination="false"
        >
            <span slot="name" slot-scope="name, record">  <a-avatar :src=" '/avatar/'+ record.no + '.png'"/>    {{name}}</span>
        </a-table>

    </div>
</template>
<script>
    import {Battle, HeroBuilders, BattleProperties} from '../../core'

    const columns = [
        {
            title: '',
            dataIndex: 'no',
        },
        {
            title: '',
            width: '20%',
            key: 'name',
            dataIndex: 'name',
            scopedSlots: {customRender: 'name'},
        },
        {
            title: '',
            dataIndex: 'teamId',
        },
        {
            title: '',
            dataIndex: 'max_hp',
        },
        {
            title: '',
            dataIndex: 'atk',
        },
        {
            title: '',
            dataIndex: 'def',
        },
        {
            title: '',
            dataIndex: 'spd',
        },
        {
            title: '',
            dataIndex: 'cri',
        },
        {
            title: '',
            dataIndex: 'cri_dmg',
        },
        {
            title: '',
            dataIndex: 'eft_hit',
        },
        {
            title: '',
            dataIndex: 'eft_res',
        },
    ];


    export default {
        data() {
        

            return {
                count: 100,
                winner0: 0,
                winner1: 0,
                error: 0,
                data: this.$store.state.team0.concat(this.$store.state.team1),
                columns,
                total: 0,
            }
        },
        methods: {
            getWinner() {
                this.winner0 = this.winner1 = this.process = 0;
                this.total = this.count;
                const fn = () => {
                    if (!this.total) return;
                    this.total--
                    const battle = new Battle(this.$store.state.team0.concat(this.$store.state.team1));
                    // eslint-disable-next-line no-empty
                    while (battle.process()) {
                    }
                    if (battle.winner === 0) this.winner0++;
                    else if (battle.winner === 1) this.winner1++;
                    else this.error++;
                    setTimeout(fn, 10);
                }
                setTimeout(fn, 10);

            }
        }

    }
</script>
<style>
    .predict {
        display: flex;
        justify-content: center;
